<template>
  <div class="container pt-100 mb-5 ">
    <!-- <h3 v-for="i in data" :key="i" >{{i}}</h3>
    <li :key="item" v-for="item in data">
      {{ item }}
    </li> -->
    <div class="row">
      <div class="col-md-12">
          <h5 class="offset-sm-5 mt-5">Aktif Client Bilgileri</h5>
          <table class="table table-bordered">
              <thead>
                  <tr>
                      <th>Socket ID</th>
                      <th>IP Adresi</th>
                      <th>Bağlantı zamanı</th>
                  </tr>
              </thead>
              <tbody>
                  <tr :key="item" v-for="(item,key) in socketList">
                      <td @click="clickSocketID(item)">{{ item }}</td>
                      <td>{{ socketIPList[key] }}</td>
                      <td>{{ socketTimeList[key] }}</td>
                  </tr>
              </tbody>
          </table>
      </div>
    </div>
    <div class="col-md-4">
      <div class="card" id="gonderGelsin">
          <div class="card-header">
              <h5>
                  Measj Gönder
              </h5>
          </div>
          <div class="card-body">
                  <div class="form-group row"><label class="col-sm-12 col-form-label" for="kirlibara">Client Id</label>
                      <div class="col-sm-12 pr-0">
                        <input class="form-control" id="clientid" type="text" name="kbara" required="required" v-model="clientID" /></div>
                  </div>
                  <div class="form-group row"><label class="col-sm-12 col-form-label" for="temizbara">Mesaj</label>
                      <div class="col-sm-12 pr-0">
                        <input class="form-control" id="message" type="text" name="tbara" required="required" v-model="message" /></div>
                  </div>
                  <div class="form-group row">
                      <div class="col-sm-5 offset-sm-1" id="nesne">
                        <input @click="sendMessage" class="btn btn-primary" type="submit" value="Kaydet" /></div>
                  </div>
          </div>
      </div>
  </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      clientID: null,
      message: null,
    }
  },
  methods:{
    sendMessage(){
      this.$socket.emit("sendMessage", this.clientID, this.message);
      this.clientID = null;
      this.message = null;
    },
    clickSocketID(id){
      this.clientID = id;
    }
  },
  computed:{
    socketList(){
      return this.$store.state.socketList;
    },
    socketIPList(){
      return this.$store.state.socketIPList;
    },
    socketTimeList(){
      return this.$store.state.socketTimeList;
    },
  },
  mounted(){
    this.$socket.on("getSocketID", (socketList,socketIPList,socketTimeList) =>{
      //this.$store.state.online = data;
      this.$store.state.socketList = socketList;
      this.$store.state.socketIPList = socketIPList;
      this.$store.state.socketTimeList = socketTimeList;

      console.log(socketTimeList)
      console.log(socketIPList)
      console.log(socketList)
    })
  },
}
</script>

